import pluginConfig from '../cc-plugin.config';
import CCP from 'cc-plugin/src/ccp/entry-main';
import Fs from 'fs';
import Path from 'path';
// @ts-ignore
import code from '-!raw-loader!./main_code.js';
CCP.init(pluginConfig, {
    load: () => {
        return 'plugin-load';
    },
    builder: {
        onAfterBuild(target) {
            if (!CCP.isV2) {
                console.warn('3.x版本暂未适配，请手动处理相关脚本');
                return;
            }
            CCP.Adaptation.log('[HotUpdateTools] build platform:' + target.platform);
            if (CCP.Adaptation.Builder.isNativePlatform(target.platform)) {
                if (code && code.length) {
                    if (target.md5Cache) {
                        CCP.Adaptation.log(`暂时不支持：使用md5选项进行构建`);
                        return;
                    }
                    const url = Path.join(Path.normalize(target.outputPath), 'main.js');
                    if (Fs.existsSync(url)) {
                        const data = Fs.readFileSync(url, 'utf8');
                        Fs.writeFileSync(url, `${code}\n${data}`);
                        CCP.Adaptation.log('[HotUpdateTools] SearchPath updated in built main.js for hot update');
                    }
                    else {
                        CCP.Adaptation.log(`[[HotUpdateTools]] 没有发现main.js文件`);
                    }
                    // let time = new Date().getTime();
                    // Editor.Ipc.sendToPanel(
                    //     'hot-update-tools',
                    //     'hot-update-tools:onBuildFinished',
                    //     time,
                    // );
                    // let CfgUtil = Editor.require(
                    //     'packages://hot-update-tools/core/CfgUtil.js',
                    // );
                    // CfgUtil.updateBuildTimeByMain(time);
                }
            }
            else {
                CCP.Adaptation.log(`[HotUpdateTools] don't need update main.js, platform: ${target.platform}`);
            }
        },
    },
    messages: {
        showPanel() {
            CCP.Adaptation.Panel.open('self.main');
        },
    }
});
