import { Emitter } from '../index';
export class IUiMenuItem {
    constructor(name, callback = null, enabled = true) {
        this.name = '';
        this.enabled = true;
        this.callback = null;
        this.name = name;
        this.callback = callback;
        this.enabled = enabled;
    }
}
export const Msg = {
    ShowMenu: 'show-menu',
};
export function showMenuByMouseEvent(event, newMenus) {
    const options = {
        x: event.clientX + 2,
        y: Math.abs(event.clientY),
    };
    Emitter.emit(Msg.ShowMenu, options, newMenus || []);
}
