import { defineComponent, ref } from 'vue';
import { Transition } from './transition';
export default defineComponent({
    name: 'cc-button',
    props: {
        transition: {
            type: String,
            default: 'color',
            validator: (value) => {
                return !!['color', 'sprite'].find(el => el === value);
            }
        },
        color: {
            type: String,
        },
        texture: {
            type: String
        }
    },
    emits: [],
    setup(props, { emit }) {
        const isClick = ref(false);
        const isHover = ref(false);
        const transition = new Transition(props.transition, props);
        return {
            theme: transition.theme,
            isClick,
            onMouseup() {
                isClick.value = false;
                transition.onMouseup();
            },
            onMousedown() {
                isClick.value = true;
                transition.onMousedown();
            },
            onMouseenter() {
                isHover.value = true;
                transition.onMouseenter();
            },
            onMouseleave() {
                isHover.value = false;
                transition.onMouseleave();
            },
        };
    },
});
